import React from "react";
import { Typography, Divider } from "antd";
import {DocTable} from "../../components";

const { Title} = Typography;

const DocTablePage = () => {
  return (
    <div>
      <Title level={2}></Title>
      <Divider />
      <code>DocTable</code>
      <Divider />

      <DocTable style={{width: "500px"}} columns={[
        {
          title: "",
          dataIndex: "name",
          width: 150,
          render: (data) => {
            return <code>{data}</code>
          }
        },
        {
          title: "",
          dataIndex: "desc"
        },
        {
          title: "",
          dataIndex: "age"
        }
      ]} dataSource={[
        {
          name: "p",
          desc: "",
          age: 18
        },
        {
          name: "link",
          desc: "",
          age: 19
        }
      ]}/>
    </div>
  );
};

export default DocTablePage;
